import router from '@/router'

import store from '../store'

router.beforeEach((to, from, next) => {
  if (to.meta.title) document.title = to.meta.title
  if (to.meta.isLogin) {
    // console.log(store)
    if (store.state.user.userName) {
      next()
    } else {
      next('/login')
    }
  } else {
    if (store.state.user.userName) {
      next('/')
    } else {
      next()
    }
  }
})



