import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [{
      path: '/',
      name: 'AdminView',
      component: () => import('@/views/Admin'),
      redirect:'/users',
      meta: {
        isLogin: true,
        title: '管理首页'
      },
      children: [{
        path: 'users',
        name: 'GteUserView',
        component: () => import('@/views/GetUserView'),
        meta: {
          isLogin: true,
          title: '查询用户'
        }
      }]
    },
    {
      path: '/login',
      name: 'LoginView',
      component: () => import('@/views/Login'),
      meta: {
        isLogin: false,
        title: '用户登录'
      }
    },
    {
      path: '/reg',
      name: 'RegisterView',
      component: () => import('@/views/Register'),
      meta: {
        isLogin: false,
        title: '用户注册'
      }
    }
  ]
})



